.dashboardContainer{display:flex;height:100vh}.dashboardContainer.light{background-color:var(--surface)}.dashboardContainer.dark{background-color:var(--dark)}.sidebar{width:250px;height:100%;background-color:#333;color:#fff;position:fixed;top:0;left:0;padding:20px}.contentArea{flex:1;padding:1rem;margin-left:250px;overflow-y:auto;display:flex;flex-direction:column;gap:1rem}.contentSplit{display:flex;gap:1rem}.contentArea2{flex:2;padding:1rem;border-radius:12px}.contentArea2.light{background-color:var(--white)}.contentArea2.dark{background-color:var(--dark2)}.contentArea3{flex:1;padding:1rem;border-radius:.5rem}.contentArea3.light{background-color:var(--white)}.contentArea3.dark{background-color:var(--dark2)}